import App from "./App"
import Leagues from "./Components/Leagues"
import EditLeague from "./Components/EditLeague"
import Team from "./Components/Team"
import EditTeam from "./Components/EditTeam"
import Teams from "./Components/Teams"

const routes = [{
  id: "default",
  path: "/",
  element: <App />,
  children:[{
    id: "leaguesapp",
    path: "leagues",
    element: <Leagues/>
  },{
    id: "league",
    path: "league/:id",
    element: <EditLeague />
  },{
    id: "teams",
    path: "teams",
    element: <Teams />
  },{
    id: "team",
    path: "team/:id",
    element: <Team />
  },{
    id: "editteam",
    path: "editteam/:id",
    element: <EditTeam />
  }]
}]

export default routes