import { createColumnHelper, flexRender, getCoreRowModel, useReactTable } from "@tanstack/react-table"

const shortView = ['name', 'players', 'actions']

const LeaguesTable = ({leaguesList, short, actionsAccessor}) => {
  const columnHelper = createColumnHelper()
  const columns = [
    columnHelper.accessor('name', {
      cell: info => <div className="cell">{info.getValue()}</div>,
    }),
    columnHelper.accessor('active', {
      cell: info => <div className="right">{info.getValue() ? '' : ''}</div>,
    }),
    columnHelper.accessor('archived', {
      cell: info => <div className="right">{info.getValue() ? '' : ''}</div>,
    }),
    columnHelper.accessor('isInternational', {
      cell: info => <div className="right">{info.getValue() ? '' : ''}</div>,
    }),
    columnHelper.accessor('players', {
      cell: info => <div className="right">{info.getValue()}</div>,
    }),
    actionsAccessor
  ].filter(col=> !short || shortView.includes(col.accessorKey))
  console.log(columns)
  
  const table = useReactTable({
    columns,
    data: leaguesList,
    getCoreRowModel: getCoreRowModel(),
  })
  return (
    <>
      <h2>Leagues</h2>
      <table id="table">
        <thead>
          {table.getHeaderGroups().map(headerGroup => (
            <tr key={headerGroup.id}>
              {headerGroup.headers.map(header => (
                <th key={header.id}>
                  {header.isPlaceholder
                    ? null
                    : flexRender(
                        header.column.columnDef.header,
                        header.getContext()
                      )}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody>
          {table.getRowModel().rows.map(row => (
            <tr key={row.id}>
              {row.getVisibleCells().map(cell => (
                <td key={cell.id}>
                  {flexRender(cell.column.columnDef.cell, cell.getContext())}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
        <tfoot>
          {table.getFooterGroups().map(footerGroup => (
            <tr key={footerGroup.id}>
              {footerGroup.headers.map(header => (
                <th key={header.id}>
                  {header.isPlaceholder
                    ? null
                    : flexRender(
                        header.column.columnDef.footer,
                        header.getContext()
                      )}
                </th>
              ))}
            </tr>
          ))}
        </tfoot>
      </table>
    </>
  )
}

export default LeaguesTable