import { useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import { Field, Formik, Form } from "formik"
import { getTeam, updateTeam } from "../util/fetcher.js"
import LeaguesTable from "./LeaguesTable.js";
import { createColumnHelper } from "@tanstack/react-table";
import { Button } from "@mui/material";
import PlayersList from "./PlayersList.js";
import toast from 'react-hot-toast';

const transferBanTypes = {
  none: 'Open to transfers',
  exitban: "Players can't leave the team",
  transferban: "Full transfer ban",
  freeagents: "Only accept free agents",
}

const EditTeam = () => {
  const {id} = useParams()
  const columnHelper = createColumnHelper()
  const actionsAccessor = columnHelper.accessor('actions', {
    cell: info => <div className="right"><Button>Remove</Button></div>,
  })
  const [team, setTeam] = useState({})
  const [players, setPlayers] = useState([])
  const [contracts, setContracts] = useState([])
  const [leagues, setLeagues] = useState([])
  useEffect(()=> {
    const initTeam = async () => {
      const fetchedTeam = await getTeam(id)
      setTeam(fetchedTeam.team)
      setPlayers(fetchedTeam.players) 
      setContracts(fetchedTeam.contracts)
      setLeagues(fetchedTeam.leagues)
    }
    initTeam()
  }, [id])
  return (
    <div className="TeamPage">
      {!team ? <h2>Team page is loading...</h2> : (
        <>
          <div className="col2">
            <Formik initialValues={team}
              onSubmit={async (values) => {
                const updatedTeam = {logo: values.logo, name: values.name, shortName: values.shortName, city: values.city, description: values.description, channel: values.channel, flag: values.flag}
                const response = await updateTeam(id, updatedTeam)
                const data = await response.json()
                setTeam({...team, ...data})
                console.log('Team updated', data)
                toast.success('Team updated')
              }}
              enableReinitialize={true}
            >
              {({ isSubmitting }) => (
                <Form>
                  <div><img src={team.logo} alt="Club logo" /></div>
                  <div>Logo: <Field name="logo" size="100" type="text"/></div>
                  <div><a href={`https://discord.com/channels/1072193923100966992/${team.teamMsg}`} rel="noreferrer" target="_blank">Entry in #clubs</a></div>
                  <div><Field name="name" type="text"/> - <Field name="shortName" type="text" /></div>
                  <div>Budget: {new Intl.NumberFormat('en-US').format(team.budget)}</div>
                  <div>Transfer status: {transferBanTypes[team.transferBan||'none']}</div>
                  <div><Field name="flag" type="text"/> <Field name="city" type="text"/></div>
                  <div>Palmars: <Field name="description" type="text"/></div>
                  <div>Channel: <Field name="channel" type="text"/> - Link: <a href={`https://discord.com/channels/1072193923100966992/${team.channel}`} rel="noreferrer" target="_blank">Discord</a></div>
                  <button type="submit" disabled={isSubmitting}>Submit</button>
                  <PlayersList players={players} contracts={contracts} />
                </Form>
              )}
            </Formik>
          </div>
          <div className="col2">
            <LeaguesTable leaguesList={leagues} short={true} actionsAccessor={actionsAccessor}/>
          </div>
        </>
      )}
    </div>
    )
}

export default EditTeam

