import { useCallback, useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import { Field, Form, Formik } from "formik"
import { getLeague, getTeams, updateLeagueStatus } from "../util/fetcher.js"
import { Button, Link } from "@mui/material"
import { Link as RouterLink } from "react-router-dom"
import TeamsTable from "./TeamsTable.js";
import { createColumnHelper } from "@tanstack/react-table";

const League = () => {
  const {id} = useParams()
  const [activePage, setActivePage] = useState(false)
  const [league, setLeague] = useState({})
  const [teams, setTeamsList] = useState([])
  const [leagueTeams, setLeagueTeams] = useState([])
  const initLeague = useCallback(async() => {
    const {league:leagueConfig, leagueTeams} = await getLeague(id)
    console.log(leagueConfig)
    setLeague(leagueConfig)
    const teamsResp = await getTeams()
    const {teams=[], leagues=[]} = teamsResp || {}
    setTeamsList(teams)
    setLeagueTeams(leagues)
    setActivePage(true)
  }, [id, setLeague, setActivePage, setLeagueTeams, setTeamsList])
  const updateLeagueStatusAction = useCallback(async(active) => {
    setActivePage(false)
    await updateLeagueStatus(league.value, active)
    return initLeague()
  }, [initLeague, league, setActivePage])
  useEffect(()=> {
    initLeague()
  }, [id, initLeague])
  const columnHelper = createColumnHelper()
  const removeFromLeague = (teamId) => {
    setActivePage(false)
    
    //call api to remove team from league
    //refresh list
  }
  const actionsAccessor = columnHelper.accessor('actions', {
    cell: info => <div className="right">{
    <Button variant="outlined" onClick={(e)=>removeFromLeague(info.row.original.id)}> Remove</Button>
    } <Link component={RouterLink} to={`/team/${info.row.original.id}`}>View</Link>
    </div>,
  })
  return (
    <div className="TeamPage">
      {!league ? <h2>League page is loading...</h2> : (
        <>
          <div className="col2">
            <Formik
              initialValues={league}
              enableReinitialize={true}
              onSubmit={async(values)=>{
                const {name, active, currentMatchDay, players, isInternational} = values
                console.log('submit', values)
              }}
            >
              {({isSubmitting}) => (
                <Form>
                  <div>Name: <Field name="name" type="text" /></div>
                  <h3>Active: {league.active ? '' : ''} <Button disabled={!activePage} onClick={()=>updateLeagueStatusAction(league.active ? false : true)}>Change</Button></h3>
                  <div>Current Matchday: {league.currentMatchDay}</div>
                  <div>Players: <Field name="players" type="number" />, competition type: {league.isInternational ? 'Nations' : 'Clubs'}</div>
                  <div>Fixtures ChannelId: <Field name="channel" type="number" /></div>
                  <div>Standings ChannelId: <Field name="standingsChannel" type="number" /></div>
                  <div>Standings MessageId: <Field name="standingsMsg" type="number" /></div>
                  <div>Standings sorting method (head 2 head is default): <label><Field type="radio" name="sorting" value="head2head" /> head to head</label> - <label><Field type="radio" name="sorting" value="goaldiff" /> Goal difference</label></div>
                  <div><input type="submit" disabled={isSubmitting} /></div>
              </Form>
              )}
            </Formik>
          </div>
            
          <div className="col2">
            <TeamsTable teams={teams} actionsAccessor={actionsAccessor}/>
          </div>
          </>
      )}
    </div>
    )
}

export default League