import { useCallback, useEffect, useState } from "react"
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline'
import DisabledByDefaultIcon from '@mui/icons-material/DisabledByDefault'
import { getTeams, updateTeamStatus } from "../util/fetcher.js"
import {
  getSortedRowModel,
  createColumnHelper,
  flexRender,
  getCoreRowModel,
  useReactTable,
} from '@tanstack/react-table'
import { Button, Link } from "@mui/material"
import { Link as RouterLink } from "react-router-dom"

const columns = [{
  id: 'name',
},{
  id: 'active',
},{
  id: 'archived',
},{
  id: 'isInternational',
},{
  id: 'players'
}]

const Teams = () => {
  const [teamsList, setTeamsList] = useState([])
  const [leagues, setLeagues] = useState([])
  const [sorting, setSorting] = useState([])
  const initTeams = useCallback(async()=> {
    const teamsResp = await getTeams()
    const {teams=[], leagues=[]} = teamsResp || {}
    setTeamsList(teams)
    setLeagues(leagues)
  }, [setLeagues, setTeamsList])
  useEffect(()=> {
    initTeams()
  },[initTeams])
  const changeTeamStatus = useCallback(async (event, team, active) => {
    event.target.disabled = true
    await updateTeamStatus(team, active)
    initTeams()
  }, [initTeams])
  
  const columnHelper = createColumnHelper()
  const columns = [
    columnHelper.accessor('icon', {
      cell: info => <div className="cell"><img src={`https://cdn.discordapp.com/role-icons/${info.row.original.id}/${info.getValue()}.png`} alt={info.row.original.shortName}/></div>,
    }),
    columnHelper.accessor('name', {
      cell: info => <div className="cell">{info.getValue()}</div>,
    }),
    columnHelper.accessor('shortName', {
      cell: info => <div className="cell">{info.getValue()}</div>,
    }),
    columnHelper.accessor('budget', {
      cell: info => <div className="right">{info.getValue()}</div>,
    }),
    columnHelper.accessor('transferBan', {
      cell: info => <div className="right">{info.getValue()}</div>,
    }),
    columnHelper.accessor('active', {
      cell: info => <div className="right">{info.getValue() ? '' : ''}</div>,
    }),
    columnHelper.accessor('actions', {
      cell: info => <div className="right">{info.row.original.active ? 
        <Button variant="outlined" onClick={(e)=>changeTeamStatus(e, info.row.original.id, false)} startIcon={<DisabledByDefaultIcon />}>Disable</Button>
      : <Button variant="outlined" onClick={(e)=>changeTeamStatus(e, info.row.original.id, true)} startIcon={<CheckCircleOutlineIcon />}>Enable</Button>
      } <Link component={RouterLink} to={`/editteam/${info.row.original.id}`}>Edit</Link>
      </div>,
    }),
  ]
  const table = useReactTable({
    columns,
    data: teamsList,
    getCoreRowModel: getCoreRowModel(),
    getSortedRowModel: getSortedRowModel(), //client-side sorting
    onSortingChange: setSorting, //optionally control sorting state in your own scope for easy access
    //no need to pass pageCount or rowCount with client-side pagination as it is calculated automatically
    state: {
      sorting,
    },
  })
  return (
    <div className="LeaguePage">
      <h2>Teams</h2>
      <table id="table">
        <thead>
          {table.getHeaderGroups().map(headerGroup => (
            <tr key={headerGroup.id}>
              {headerGroup.headers.map(header => (
                <th key={header.id}>
                  {header.isPlaceholder ? null
                  : (
                    <div
                      className={
                        header.column.getCanSort()
                          ? 'cursor-pointer select-none'
                          : ''
                      }
                      onClick={header.column.getToggleSortingHandler()}
                      title={
                        header.column.getCanSort()
                          ? header.column.getNextSortingOrder() === 'asc'
                            ? 'Sort ascending'
                            : header.column.getNextSortingOrder() === 'desc'
                              ? 'Sort descending'
                              : 'Clear sort'
                          : undefined
                      }>
                      {flexRender(
                        header.column.columnDef.header,
                        header.getContext()
                      )}</div>
                    )
                  }
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody>
          {table.getRowModel().rows.map(row => (
            <tr key={row.id}>
              {row.getVisibleCells().map(cell => (
                <td key={cell.id}>
                  {flexRender(cell.column.columnDef.cell, cell.getContext())}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
    )
}

export default Teams