const host = '/web/api'
//const host = 'https://pso.shinmugen.net/api'
//const host2 = 'http://localhost:8080/api'
export const getLeagues = async () => apiCall('/leagues')
export const getTeams = async () => apiCall('/allteams')
export const getTeam = async (id) => apiCall('/teamplayers?id='+id)
export const getLeague = async (id) => apiCall('/leagueconfig?league='+id)

export const updateTeamStatus = async (team, active) => put('/teamstatus', {team, active})
export const updateLeagueStatus = async (league, active) => put('/activateleague', {league, active})
export const updateTeam = async (id, team) => put(`/team/${id}`, team)
const put = async(path, body) => apiCall(path, {
  method: 'PUT',
  headers: { 'Content-Type': 'application/json; charset=UTF-8', },
  body: JSON.stringify(body)
})
const post = async(path, body) => apiCall(path, {
  method: 'POST',
  headers: { 'Content-Type': 'application/json; charset=UTF-8', },
  body: JSON.stringify(body)
})

export const apiCall = async(path, options={}) => {
  try {
    console.log(host)
    const callResp = await fetch(host + path, options)
    return callResp.json()
  } catch(e) {
    console.error(e)
  }
}