import { createColumnHelper } from "@tanstack/react-table"
import TableComponent from "./TableComponent"

const shortView = ['name', 'players', 'actions']

const TeamsTable = ({teams, actionsAccessor}) => {
  const columnHelper = createColumnHelper()
  const columns = [
    columnHelper.accessor('name', {
      cell: info => <div className="cell">{info.getValue()}</div>,
    }),
    columnHelper.accessor('active', {
      cell: info => <div className="right">{info.getValue() ? '' : ''}</div>,
    }),
    actionsAccessor
  ]
  
  return (
    <>
      <h2>League Teams</h2>
      <TableComponent data={teams} columns={columns}/>
    </>
  )
}

export default TeamsTable