import { useCallback, useEffect, useState } from "react"
import { Link as RouterLink } from "react-router-dom"
import { Button, Link } from "@mui/material"
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline'
import DisabledByDefaultIcon from '@mui/icons-material/DisabledByDefault'
import { createColumnHelper } from "@tanstack/react-table"
import { getLeagues, updateLeagueStatus } from "../util/fetcher.js"
import LeaguesTable from "./LeaguesTable.js"

const Leagues = () => {
  const columnHelper = createColumnHelper()
  const [leaguesList, setLeaguesList] = useState([])
  const [activePage, setActivePage] = useState(false)
  const initLeagues = useCallback(async () => {
    const leagues = await getLeagues()
    setLeaguesList(leagues)
    setActivePage(true)
  }, [setLeaguesList, setActivePage])
  const updateLeagueStatusAction = useCallback(async(league, active) => {
    setActivePage(false)
    await updateLeagueStatus(league, active)
    return initLeagues()
  }, [initLeagues, setActivePage])
  useEffect(()=> {
    initLeagues()
  },[initLeagues])
  const actionsAccessor = columnHelper.accessor('actions', {
    cell: info => <div className="right">{info.row.original.active ? 
      <Button disabled={!activePage} variant="outlined" onClick={(e)=>updateLeagueStatusAction(info.row.original.value, false)} startIcon={<DisabledByDefaultIcon />}>Disable</Button>
    : <Button disabled={!activePage} variant="outlined" onClick={(e)=>updateLeagueStatusAction(info.row.original.value, true)} startIcon={<CheckCircleOutlineIcon />}>Enable</Button>
    } <Link component={RouterLink} to={`/league/${info.row.original.value}`}>Edit</Link></div>,
  })
  return <div className="LeaguePage"><LeaguesTable leaguesList={leaguesList} actionsAccessor={actionsAccessor} /></div>
}

export default Leagues