import { useEffect, useState } from "react"

const clubManagerRole = '1072620773434462318'

const PlayersList = ({players=[], contracts=[]}) => {
  const [playerList, setPlayerList] = useState([])
  useEffect(()=> {
    setPlayerList(players.map(player=> ({
      ...player,
      isCoach: player.roles.includes(clubManagerRole),
      contract: contracts.find(contract=>contract.playerId === player?.user?.id)
    })).sort((a,b)=> {
      if (a.isCoach === b.isCoach)
        return 0
      else if(a.isCoach) {
        return -1
      } else {
        return 1
      }
    }))
  },[players, contracts])

  return (<ul>
    {playerList.map(player=> (<li key={player.user.id}>{player.isCoach ? '': ''}{player.nick} - {player.isLoan ? `LOAN Season ${player.contract.until} - beginning of phase ${player.contract.phase}` : `Season ${player.contract.until-1}`}</li>))}
  </ul>)
}

export default PlayersList